.btn {
  display: inline-block;
  margin-bottom: 1em;
  padding: 15px 1.5em;
  border-radius: 44px;
  background: white;
  font-size: 14px;
  text-align: center;
  transition: 0.2s;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.16);
}

.btn-size-3 {
  min-width: 3em;
}

.btn-size-6 {
  min-width: 6em;
}

.btn-size-9 {
  min-width: 9em;
}

.btn-size-block {
  display: block;
}

.btn.btn-theme {
  background-color: #4FB200;
  color: white;
}

.btn.btn-danger {
  background-color: #FF0000;
  color: white;
}

.btn.btn-success {
  background-color: #006CFF;
  color: white;
}

.btn.btn-b {
  border: solid 1px black;
  color: black;
}

.btn.btn-b-theme {
  border: solid 1px #4FB200;
  color: #4FB200;
}

.btn.btn-b-danger {
  border: solid 1px #FF0000;
  color: #FF0000;
}

.btn.btn-b-success {
  border: solid 1px #006CFF;
  color: #006CFF;
}

.btn:hover {
  cursor: hand;
  opacity: 0.6;
}

.card {
  margin-bottom: 1em;
  box-shadow: 0 0 15px -6px #00000073;
  background-color: white;
  width: 17em;
}

.card-img {
  width: 100%;
}

.card-body {
  padding: 1.5em;
}

.card p {
  margin-bottom: 1em;
}

label {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.input-wrap {
  display: inline-block;
  margin: 0 16px 16px 0;
  padding-top: 24px;
}

.input-wrap .input-label {
  z-index: 0;
  display: inline-block;
  margin: 0;
  position: relative;
  width: 240px;
  height: 46px;
  border-radius: 45px;
  box-sizing: border-box;
  background: transparent;
}

.input-wrap .input-label label {
  position: absolute;
  z-index: 0;
  color: gray;
  transition: .2s;
  font-size: 20px;
  box-sizing: border-box;
}

.input-wrap .input-label input {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0);
  box-sizing: border-box;
}

.input-wrap .input-label input:placeholder-shown + label {
  top: 12px;
  left: 0px;
  right: 0;
  bottom: 0;
}

.input-wrap .input-label input:not(placeholder-shown) + label {
  top: -24px;
  left: -12px;
  right: 0;
  transform: scale(0.8);
}

.input-wrap .input-label.error input {
  box-sizing: border-box;
  box-shadow: 0 0 0 0.2rem rgba(255, 0, 0, 0.5);
}

.input-wrap .input-label.error + p {
  margin: 8px 0 0 16px;
  color: #FF0000;
  font-size: 14px;
}

input {
  box-sizing: border-box;
}

input[type] {
  appearance: none;
}

input:focus {
  outline: none;
  box-shadow: 0 0 0 0.2rem rgba(0, 108, 255, 0.5) !important;
}

input[type="text"], input[type="password"] {
  padding: 15px 20px;
  font-size: 16px;
  border-radius: 45px;
  border: solid 1px black;
}

input[type="radio"] {
  margin: 5px;
  position: relative;
  border-radius: 50%;
  border: 1px solid black;
  width: 24px;
  height: 24px;
}

input[type="radio"]:before {
  content: "";
  display: block;
  box-shadow: 0 0 2px 0.2rem rgba(0, 108, 255, 0.5);
  border-radius: 50%;
  position: absolute;
  left: 7px;
  top: 7px;
  margin: auto;
  width: 8px;
  height: 8px;
  background: #006CFF;
  transform: scale(0);
  transition: .2s;
}

input[type="radio"]:checked {
  border: 1px solid #006CFF;
  box-shadow: 0 0 0 0.2rem rgba(0, 108, 255, 0.5);
}

input[type="radio"]:checked:before {
  transform: scale(1);
}

input[type="checkbox"] {
  margin: 5px;
  position: relative;
  border: 1px solid black;
  border-radius: 5px;
  width: 24px;
  height: 24px;
  box-shadow: 0 0 0 0;
}

input[type="checkbox"]:before {
  content: "";
  display: block;
  position: absolute;
  left: 7px;
  top: 2px;
  margin: auto;
  width: 5px;
  height: 13px;
  border-right: 3px solid #006CFF;
  border-bottom: 3px solid #006CFF;
  transform: scale(0) rotate(90deg);
  transition: .2s;
}

input[type="checkbox"]:checked {
  border: 1px solid #006CFF;
  box-shadow: 0 0 0 0.2rem rgba(0, 108, 255, 0.5);
}

input[type="checkbox"]:checked:before {
  transform: scale(1) rotate(45deg);
}

input[type="submit"] {
  box-shadow: 0 0 0 0;
}

.content-main {
  background: white;
  margin: .5em;
  padding: .5em;
}

.content-block {
  margin-bottom: 1em;
}

.content-block.center {
  text-align: center;
}

.content-block .content-block-horizontal {
  text-align: left;
}

.content-block-horizontal {
  display: inline-block;
  margin-bottom: 1em;
  vertical-align: top;
}

.content-block-horizontal.align-middle {
  vertical-align: middle;
}

.content-block-horizontal.align-bottom {
  vertical-align: bottom;
}

.content-indent {
  margin-left: 1em;
}

.text-center {
  text-align: center;
}

@media screen and (min-width: 415px) {
  .content-indent {
    margin-left: 2em;
  }
}

@media screen and (min-width: 960px) {
  .content-main {
    margin: .5em auto;
    max-width: 50em;
  }
}

.table-wrap {
  overflow: scroll;
  width: 100%;
}

table {
  border-collapse: collapse;
  margin-bottom: 16px;
  box-shadow: 0 0 15px -6px #00000073;
  background-color: white;
  width: 600px;
}

table.center {
  margin: 0 auto 16px;
}

table tr {
  box-sizing: border-box;
}

table tr:not(:nth-of-type(1)) {
  border-top: 1px solid black;
}

table th {
  background-color: #4FB200;
  color: white;
  padding: 1rem;
  white-space: nowrap;
}

table td {
  padding: .75rem;
}

table .edit {
  text-align: center;
}

table .edit .btn {
  margin: 0;
  padding: 6px;
}

table .edit .btn:nth-of-type(1) {
  margin-right: 8px;
}

