@charset "utf-8";

// モバイルファースト
.content-main{
    background:white;
    margin:.5em;
    padding:.5em;
}

.content-block{
    // padding: 0 .5em;
    margin-bottom: 1em;
    &.center{
        text-align:center;
    }
    .content-block-horizontal{
        text-align:left
    }
}

.content-block-horizontal{
    display:inline-block;
    margin-bottom: 1em;
    vertical-align: top;
    &.align-middle{
        vertical-align: middle;
    }
    &.align-bottom{
        vertical-align: bottom;
    }
}

.content-indent{
    margin-left:$space;
}

.text-center{
    text-align:center;
}


// タブレット415pxから959pxまで
@media screen and (min-width:$breakpoint-m){
    .content-indent{
        margin-left:$space-m;
    }    
}

//　PC用デザイン960pxから
@media screen and (min-width:$breakpoint-l){
    .content-main{
        margin: .5em auto;
        max-width: 50em;
    }    
}
